<template>
  <div class="searchCell">
    <van-cell>
      <template #icon>
        <div class="icon">
          <van-icon :name="icon" />
        </div>
      </template>
      <template #title>
        <div class="title" @click="goSeatch" v-html="title"></div>
      </template>
      <template #right-icon>
        <div class="right-icon" @click="delClick">
          <van-icon :name="right" />
        </div>
      </template>
    </van-cell>
  </div>
</template>

<script>
export default {
  name: 'searchCell',
  props: ['icon', 'title', 'right'],
  methods: {
    delClick () {
      this.$emit('delClick')
    },
    goSeatch () {
      this.$emit('goSearch', this.title)
    }
  }
}
</script>

